/* Keyboard.css */
.keyboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  width: 100%; /* Make keyboard take full width */
}

.keyboard-row {
  display: flex;
  justify-content: center;
  margin: 5px 0; /* Add margin between rows */
  width: 100%;
}

.key {
  background: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 15px 20px;
  margin: 5px; /* Add margin between keys */
  font-size: 16px;
  cursor: pointer;
  user-select: none;
  flex: 1; /* Make keys take equal width */
  max-width: 60px; /* Prevent keys from becoming too wide */
  text-align: center; /* Center text within the keys */
}

.key.backspace {
  flex-grow: 2; /* Make the backspace key wider */
  text-align: center;
  white-space: nowrap; /* Prevent text from wrapping */
  padding: 15px; /* Adjust padding for backspace key */
}

.key:active {
  background: #ccc;
}

@media (max-width: 600px) {
  .keyboard {
    flex-direction: column; /* Stack rows vertically on small screens */
    align-items: center; /* Center rows horizontally */
  }

  .keyboard-row {
    width: 100%; /* Ensure each row takes full width */
    margin: 3px 0; /* Decrease margin between rows for spacing */
  }

  .key {
    width: calc(20% - 6px); /* Calculate width dynamically for 5 keys per row */
    margin: 3px; /* Decrease margin between keys */
    max-width: 40px; /* Adjust max-width to fit smaller screens */
    padding: 10px; /* Adjust padding for smaller keys */
    font-size: 14px; /* Adjust font size for smaller screens */
  }

  .key.backspace {
    flex-grow: 3; /* Adjust backspace key width on smaller screens */
    max-width: 100px; /* Limit max-width for backspace key */
    padding: 10px; /* Adjust padding for backspace key */
    font-size: 14px; /* Adjust font size to ensure text fits */
  }
}





